import React from 'react';
import { useTranslation } from '../hooks/useTranslation';
import {
    Server, Shield, Globe, Check, Zap,
    HeartPulse, Settings, LifeBuoy, CreditCard
} from 'lucide-react';
import { LICENSING_BASE, AUTH_BASE, Endpoint, SectionHeader } from '../components/ApiDocsComponents';

const PosApiDocs: React.FC = () => {
    const { t, i18n } = useTranslation();
    const isRtl = i18n.language === 'ar';

    return (
        <div className="max-w-4xl mx-auto pb-20 space-y-12" dir={isRtl ? 'rtl' : 'ltr'}>

            {/* Header */}
            <div className="text-center space-y-4 py-8">
                <div className="inline-flex items-center justify-center w-16 h-16 rounded-2xl bg-gradient-to-br from-blue-500 to-indigo-600 text-white shadow-lg shadow-primary-500/20 mb-4">
                    <Server size={32} />
                </div>
                <h1 className="text-3xl md:text-4xl font-extrabold text-slate-900 dark:text-white tracking-tight">
                    {t('nav.apiDocs')}
                </h1>
                <p className="text-lg text-slate-600 dark:text-slate-300 max-w-2xl mx-auto leading-relaxed">
                    Comprehensive guide to integrating your applications with the SourcePlus Licensing System.
                </p>
            </div>

            {/* Base Info Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="p-5 rounded-2xl bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 shadow-sm">
                    <div className="flex items-center gap-2 mb-2 text-primary-600 dark:text-primary-400">
                        <Globe size={18} />
                        <span className="text-xs font-bold uppercase tracking-wider">Base URL</span>
                    </div>
                    <code className="text-sm font-mono font-bold text-slate-900 dark:text-white break-all">
                        {LICENSING_BASE}
                    </code>
                </div>

                <div className="p-5 rounded-2xl bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 shadow-sm">
                    <div className="flex items-center gap-2 mb-2 text-indigo-600 dark:text-indigo-400">
                        <Shield size={18} />
                        <span className="text-xs font-bold uppercase tracking-wider">Auth URL</span>
                    </div>
                    <code className="text-sm font-mono font-bold text-slate-900 dark:text-white break-all">
                        {AUTH_BASE}
                    </code>
                </div>

                <div className="p-5 rounded-2xl bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 shadow-sm">
                    <div className="flex items-center gap-2 mb-2 text-emerald-600 dark:text-emerald-400">
                        <Check size={18} />
                        <span className="text-xs font-bold uppercase tracking-wider">System Status</span>
                    </div>
                    <div className="flex items-center gap-2">
                        <span className="relative flex h-2.5 w-2.5">
                            <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-emerald-400 opacity-75"></span>
                            <span className="relative inline-flex rounded-full h-2.5 w-2.5 bg-emerald-500"></span>
                        </span>
                        <span className="text-sm font-bold text-slate-900 dark:text-white">Active</span>
                    </div>
                </div>
            </div>

            {/* SourcePlus POS Licensing Documentation */}

            <section>
                <SectionHeader
                    title="  (Licensing & Activation)"
                    icon={Zap}
                    description="      (POS)  ."
                />
                <div className="space-y-4">
                    <Endpoint
                        isRtl={isRtl}
                        method="POST"
                        url="/api/pos/validate"
                        title="1. :    (Prepare: Validate Serial)"
                        description=":           .      ."
                        payload={`{
  "serial": "SP-2024-XXXX-YYYY",
  "hardwareId": "Optional-HWID-Check"
}`}
                        response={`{
  "valid": true,
  "status": "active",
  "expireDate": "2024-12-31T00:00:00Z",
  "daysLeft": 200,
  "isExpired": false,
  "plan": {
     "id": "uuid-...",
     "name": "Professional",
     "durationMonths": 12,
     "features": { 
        "pos": true, 
        "inventory": true,
        "reports": true
     },
     "limits": {
        "maxUsers": 5,
        "maxBranches": 2,
        "maxInvoices": -1 
     },
     "deviceLimit": 3
  }
}`}
                    />
                    <Endpoint
                        isRtl={isRtl}
                        method="POST"
                        url="/api/pos/activate"
                        title="2.    (First-Time Activation)"
                        description=":       (Hardware ID).         ."
                        payload={`{
  "serial": "SP-2024-XXXX-YYYY",
  "hardwareId": "UUID-MAC-DISK-SERIAL",
  "deviceName": "Cashier-01",
  "appVersion": "1.0.0"
}`}
                        response={`{
  "success": true,
  "activationDate": "2024-01-01T10:00:00Z"
}`}
                    />
                    <Endpoint
                        isRtl={isRtl}
                        method="GET"
                        url="/api/pos/check-license?serial=..."
                        title="3.   (Daily Startup Check)"
                        description=":                 ."
                        response={`{
  "valid": true,
  "status": "active",
  "expireDate": "2024-12-31T00:00:00Z",
  "daysLeft": 199,
  "plan": {
    "name": "Professional",
    "deviceLimit": 3,
    "limits": {
       "maxUsers": 5,
       "maxBranches": 2,
       "maxInvoices": -1,  // -1 indicates Unlimited ()
       "maxProducts": 1000,
       "maxCustomers": -1
    }
  }
}`}
                    />
                </div>
            </section>

            {/* System Health */}
            <section>
                <SectionHeader
                    title="   (Health & Updates)"
                    icon={HeartPulse}
                    description="      ."
                />
                <div className="space-y-4">
                    <Endpoint
                        isRtl={isRtl}
                        method="POST"
                        url="/api/pos/heartbeat"
                        title="4.  :   (Runtime: Heartbeat)"
                        description=":       (  60 )        ."
                        payload={`{
  "serial": "SP-2024-XXXX-YYYY",
  "hardwareId": "UUID-MAC-DISK-SERIAL",
  "appVersion": "1.0.0"
}`}
                        response={`{
  "success": true,
  "timestamp": "2024-01-01T12:00:00Z"
}`}
                    />
                    <Endpoint
                        isRtl={isRtl}
                        method="GET"
                        url="/api/pos/check-update?version=1.0.0"
                        title="  "
                        description="        ."
                        response={`{
  "shouldUpdate": true,
  "latest": {
    "version": "1.1.0",
    "downloadUrl": "https://cdn.sourceplus.com/v1.1.0.exe",
    "releaseNotes": "Bug fixes and improvements.",
    "forceUpdate": false
  }
}`}
                    />
                </div>
            </section>

            {/* Configuration & Data */}
            <section>
                <SectionHeader
                    title="  (Configuration)"
                    icon={Settings}
                    description="    ."
                />
                <div className="space-y-4">
                    <Endpoint
                        isRtl={isRtl}
                        method="GET"
                        url="/api/pos/plans"
                        title="  (Get Plans)"
                        description="          ."
                        response={`[
  {
    "id": "uuid...",
    "name": "Professional",
    "durationMonths": 12,
    "features": { "pos": true, "inventory": true, "customers": true, "reports": true },
    "limits": { 
      "maxUsers": 5, 
      "maxBranches": 2,
      "maxInvoices": -1,  // -1 indicates Unlimited ()
      "maxProducts": 1000,
      "maxCustomers": -1
    },
    "deviceLimit": 3,
    "prices": [
       { "currency": "USD", "periodPrice": 100, "isPrimary": true },
       { "currency": "IQD", "periodPrice": 150000, "isPrimary": false }
    ]
  }
]`}
                    />
                    <Endpoint
                        isRtl={isRtl}
                        method="GET"
                        url="/api/pos/sync-config"
                        title="  (Sync Config)"
                        description="     (Remote Config)."
                        response={`{
  "maintenance_mode": false,
  "min_version": "1.0.0"
}`}
                    />
                </div>
            </section>

            {/* Support */}
            <section>
                <SectionHeader
                    title="  (Support)"
                    icon={LifeBuoy}
                    description="      ."
                />
                <div className="space-y-4">
                    <Endpoint
                        isRtl={isRtl}
                        method="POST"
                        url="/api/pos/support"
                        title="  "
                        description="    /   ."
                        payload={`{
  "serial": "SP-2024...",
  "hardwareId": "...",
  "deviceName": "POS-01",
  "systemVersion": "Windows 11",
  "phoneNumber": "0770...",
  "appVersion": "1.0.0",
  "description": "Printer not working"
}`}
                        response={`{
  "ticketId": "uuid...",
  "status": "open"
}`}
                    />
                    <Endpoint
                        isRtl={isRtl}
                        method="GET"
                        url="/api/pos/support?serial=..."
                        title="   (Ticket History)"
                        description="       ."
                        response={`[
  {
    "id": "uuid...",
    "description": "Printer Issue",
    "status": "open",
    "createdAt": "2024...",
    "replies": [
       { "message": "Please restart...", "createdAt": "..." }
    ]
  }
]`}
                    />
                </div>
            </section>

            {/* Notifications */}
            <section>
                <SectionHeader
                    title=" (Notifications)"
                    icon={CreditCard}
                    description="    ."
                />
                <div className="space-y-4">
                    <Endpoint
                        isRtl={isRtl}
                        method="GET"
                        url="/api/pos/notifications?serial=..."
                        title=" "
                        description="     (     )."
                        response={`[
  {
    "id": "uuid...",
    "title": " ",
    "body": "  ...",
    "sentAt": "2024-03-20..."
  }
]`}
                    />
                </div>
            </section>

        </div>
    );
};

export default PosApiDocs;
